.calculator {
    padding: 10px;
    border-radius: 10px;
    height: 500px;
    width: 400px;
    /* margin: auto; */
    background-color: #9fbe14;
    
    /* box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px; */
   }
   .display-box {
    font-family: 'Orbitron', sans-serif;
    background-color: #dcdbe1;
    border: solid black 0.5px;
    color: black;
    border-radius: 5px;
    width: 98%;
    height: 10%;
    text-align: right;
    font-size: larger;
   }
   .button {
    font-family: 'Orbitron', sans-serif;
    background-color: #64278f;
    color: white;
    border: solid black 0.5px;
    width: 100%;
    border-radius: 5px;
    height: 70%;
    outline: none;
    border-collapse: collapse;
   }
   
   .calculatorPage{
    /* padding: 10px; */
    border-radius: 10px;
    height: 550px;
    width: 400px;
    /* margin: auto; */
    background-color: #b2d142;
    margin: auto;
   }